@import url('https://fonts.googleapis.com/css2?family=Neucha&display=swap');
.App {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #c02f2f;
    font-family: "Neucha", cursive;
  }
  
  .heading {
    text-transform: uppercase;
    font-size: 40px;
    margin: 30px 0;
    color: white;
    z-index: 1;
    text-align: center;
  }
  
  @media (max-width: 800px) {
    .heading {
      margin: 15px 0;
      font-size: 35px;
    }
  }